
<template>
  <div class="mymapM">

    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="$router.go(-1)">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-search">
        <div class="search-map">
          <input v-model="search_key" type="text" placeholder="输入关键字搜索">
          <span v-if="search_key" class="clear" @click="search_key ='' ">
            <van-icon color="#8f8f8f" name="clear" />
          </span>
          <van-button plain type="info" size="mini" @click="keySearch()">捜索</van-button>
        </div>
      </div>
    </div>
    <!-- header end -->

    <!--捜索-->
    <div v-if="loading" class="loading">
      <van-loading type="spinner" />
    </div>

    <div v-if="!search_key" class="con-box con-map">
      <!--地图-->
      <div class="mapbox">
        <div id="container" class="map" />
        <div class="sign" />
      </div>
    </div>
    <div v-if="!search_key" class="con-box">
      <!--地址列表-->
      <div class="Hlist-box">
        <ul>
          <li v-for="(item, index) in lists" :key="index" @click="onAddressLi(item)">
            <div class="add">
              <div class="add-name"><van-icon name="location-o" />{{ item.name }}</div>
              <div class="add-address">{{ item.address }}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!--捜索列表-->
    <div v-if="search_key" class="searchlist">
      <ul>
        <li v-for="(item, index) in search_list" :key="index" @click="onSearchLi(item.location)">
          <div class="add">
            <div class="add-name"><van-icon name="location-o" />{{ item.name }}</div>
            <div class="add-address">{{ item.address }}</div>
          </div>
        </li>

      </ul>

      <div v-if="noSearchShow">
        <div class="status" style="margin-top:50%;">
          <div class="status-icon" />
          <h3>暂无结果</h3>
        </div>
      </div>
    </div>
  </div>
</template>
<script scoped>
export default {

  data() {
    return {
      center: [this.$route.params.lng, this.$route.params.lat], // 经度+纬度
      // center: [116.362875, 39.923346],
      search_key: '', // 搜索值
      lists: [], // 地点列表
      search_list: [], // 搜索结果列表
      marker: '',
      loading: false,
      noSearchShow: false // 无搜索结果提示，无搜索结果时会显示暂无搜索结果
    }
  },
  watch: {
    search_key(newv, oldv) {
      if (newv === '') {
        this.search_list = []
        this.noSearchShow = false
        setTimeout(() => {
          this.adMap()
        }, 1000)
      }
    }
  },
  mounted() {
    setTimeout(() => {
      this.adMap()
    }, 1000)
  },
  methods: {
    adMap() {
      this.loading = true
      // 初始化地图
      var map = new AMap.Map('container', {
        zoom: 14, // 缩放级别
        center: this.center, // 设置地图中心点
        resizeEnable: true // 地图初始化加载定位到当前城市
      })
      // 获取初始中心点并赋值
      var currentCenter = map.getCenter() // 此方法是获取当前地图的中心点
      this.center = [currentCenter.lng, currentCenter.lat] // 将获取到的中心点的纬度经度赋值给data的center
      console.log(this.center)

      // 创建标记
      this.marker = new AMap.Marker({
        position: new AMap.LngLat(currentCenter.lng, currentCenter.lat) // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
      })
      // 将创建的点标记添加到已有的地图实例：
      map.add(this.marker)

      // 根据地图中心点查附近地点，此方法在下方
      this.centerSearch()
      // 监听地图移动事件，并在移动结束后获取地图中心点并更新地点列表
      var moveendFun = e => {
        // 获取地图中心点
        currentCenter = map.getCenter()
        this.center = [currentCenter.lng, currentCenter.lat]
        this.marker.setPosition([currentCenter.lng, currentCenter.lat]) // 更新标记的位置
        // 根据地图中心点查附近地点
      }
      // 更新数据
      var centerSearch = () => {
        this.loading = true
        this.centerSearch()
      }

      // 绑定事件移动地图事件
      map.on('mapmove', moveendFun) // 更新标记
      map.on('moveend', centerSearch) // 更新数据
    },
    centerSearch() {
      AMap.service(['AMap.PlaceSearch'], () => {
        // 构造地点查询类
        var placeSearch = new AMap.PlaceSearch({
          type:
            '汽车服务|餐饮服务|购物服务|生活服务|体育休闲服务|医疗保健服务|住宿服务|风景名胜|商务住宅|政府机构及社会团体|科教文化服务|交通设施服务|金融保险服务|公司企业|地名地址信息', // 兴趣点类别
          pageSize: 30, // 单页显示结果条数
          pageIndex: 1, // 页码
          city: '全国', // 兴趣点城市
          autoFitView: false // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
        })
        // 根据地图中心点查附近地点
        placeSearch.searchNearBy(
          '',
          [this.center[0], this.center[1]],
          200,
          (status, result) => {
            if (status == 'complete') {
              this.lists = result.poiList.pois // 将查询到的地点赋值
              this.loading = false
            }
          }
        )
      })
    },
    keySearch() {
      this.loading = true
      AMap.service(['AMap.PlaceSearch'], () => {
        // 构造地点查询类
        var placeSearch = new AMap.PlaceSearch({
          type:
            '汽车服务|餐饮服务|购物服务|生活服务|体育休闲服务|医疗保健服务|住宿服务|风景名胜|商务住宅|政府机构及社会团体|科教文化服务|交通设施服务|金融保险服务|公司企业|地名地址信息', // 兴趣点类别
          pageSize: 30, // 单页显示结果条数
          pageIndex: 1, // 页码
          city: '全国', // 兴趣点城市
          citylimit: false, // 是否强制限制在设置的城市内搜索
          autoFitView: false // 是否自动调整地图视野使绘制的 Marker点都处于视口的可见范围
        })
        // 关键字查询
        placeSearch.search(this.search_key, (status, result) => {
          if (status == 'complete') {
            if (result.poiList.count === 0) {
              this.noSearchShow = true
            } else {
              this.search_list = result.poiList.pois // 将查询到的地点赋值
              this.noSearchShow = false
              this.loading = false
            }
          } else {
            this.search_list = []
            this.noSearchShow = true
          }
        })
      })
    },
    onAddressLi(e) {
      console.log(e)
      // this.$store.state.erp.dzxz = e.address
      // this.$store.commit('erp/SET_DZXZ', e.address)
      // console.info('this.$store.state.erp.dzxz --', this.$store.state.erp.dzxz)
      this.$bus.$emit('getAddress', e.address)
      window.localStorage.setItem('getAddress', e.address)

      this.marker.setPosition([e.location.lng, e.location.lat]) // 更新标记的位置
      this.$router.go(-1)
    },
    onSearchLi(e) {
      console.log(e.lng + '-' + e.lat)
      // window.localStorage.setItem('getAddress', e.address)
      this.$bus.$emit('getAddress', e.address)
      this.center = [e.lng, e.lat]
      console.log(this.center)
      this.search_key = ''
      // this.loading=true;
      setTimeout(() => {
        this.adMap()
      }, 1000)
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
.mapbox .map{width: 100%;height: 400px;}
.search-map{display: flex; align-items: center;position: relative;}
.search-map input{height: 26px;line-height: 26px;background: #F3F5F9;border: 0;border-radius: 2px;padding: 0 0 0 10px;margin: 0 5px 0 0;width: calc(100% - 70px);}
.search-map .van-button{position: absolute;right: 10px;}
.search-map .clear{position: absolute;right: 65px; top: 6px; z-index: 1;}
.Hlist-box{height: calc(100vh - 400px);overflow: auto;background: #fff;padding: 10px;}
.Hlist-box ul li,.searchlist ul li{border-bottom: 1px #E6F7FF solid;padding: 5px 0;margin: 0 0 5px 0;width: 100%;}
.add .add-name{font-size: 14px;display: flex; align-items: center;color: #2B3642}
.add .add-name > i{color: #666f83;font-size: 14px;margin: 0 5px 0 0;}
.add .add-address{color: #666f83;font-size: 12px;margin: 0 0 0 20px;}
.searchlist{margin: 60px 0 0 0;background: #fff;padding: 20px;}
</style>
